import React from 'react'
import { Switch, Route } from 'react-router-dom'
import GlobalFeed from './pages/globalFeed'
import Article from './pages/article'
import TagFeed from './pages/tagFeed'
import Settings from './pages/settings'
import YourFeed from './pages/yourFeed'
import CreateArticle from './pages/createArticle'
import EditArticle from './pages/editArticle'
import Authentication from './pages/authentication'
import UserProfile from './pages/userProfile'

export default () => {
    return (
        <Switch>
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/profiles/:slug`} component={UserProfile} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/profiles/:slug/favorites`} component={UserProfile} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/tags/:slug`} component={TagFeed} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/feed`} component={YourFeed} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/settings`} component={Settings} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/`} component={GlobalFeed} exact/>
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/login`} component={Authentication} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/register`} component={Authentication} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/article/new`} component={CreateArticle} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/article/:slug/edit`} component={EditArticle} />
            <Route path={`${process.env.REACT_APP_MAIN_PATH}/article/:slug`} component={Article} />
        </Switch>
    )
}